/* src/app/footer/footer.scss */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family:
    "Inter",
    Arial,
    sans-serif;
  background: #f7f6f4;
  color: #212121;
  margin: 0;
  padding: 0;
}
.shell-footer {
  width: 100%;
  background: #ffffff;
  border-top: 2px solid #ff6200;
  padding: 24px;
  display: flex;
  justify-content: end;
  align-items: center;
  font-family:
    "Inter",
    Arial,
    sans-serif;
  font-size: 14px;
  color: #212121;
  font-weight: 400;
  letter-spacing: 0.05em;
  box-shadow: 0 -2px 8px rgba(255, 98, 0, 0.05);
}
@media (max-width: 700px) {
  .shell-footer {
    padding: 12px 0;
    font-size: 12px;
  }
}
